<script>
import { ref, computed, onMounted, reactive, watch } from 'vue'
import axios from '../axios'
import { useToastStore } from '../stores/toastStore'
import { auth } from '../auth'

export default {
  setup() {
    const products = ref([])
    const isLoading = ref(true)
    const searchQuery = ref('')
    const toast = useToastStore()
    const quantities = reactive({})
    
    // Pagination state
    const currentPage = ref(1)
    const productsPerPage = ref(12)
    
    // Notify other components about cart updates
    const notifyCartUpdate = () => {
      window.dispatchEvent(new CustomEvent('cart-updated'))
    }
    
    const filteredProducts = computed(() => {
      if (!searchQuery.value) return products.value
      
      const query = searchQuery.value.toLowerCase()
      return products.value.filter(product => 
        product.name.toLowerCase().includes(query) || 
        product.description.toLowerCase().includes(query)
      )
    })
    
    // Pagination computed properties
    const totalPages = computed(() => {
      return Math.ceil(filteredProducts.value.length / productsPerPage.value)
    })
    
    const startIndex = computed(() => {
      return (currentPage.value - 1) * productsPerPage.value
    })
    
    const paginatedProducts = computed(() => {
      const start = startIndex.value
      const end = start + productsPerPage.value
      return filteredProducts.value.slice(start, end)
    })
    
    const visiblePages = computed(() => {
      const pages = []
      const start = Math.max(1, currentPage.value - 2)
      const end = Math.min(totalPages.value, currentPage.value + 2)
      
      for (let i = start; i <= end; i++) {
        pages.push(i)
      }
      return pages
    })
    
    // Reset to first page when search changes
    watch(searchQuery, () => {
      currentPage.value = 1
    })
    
    const goToPage = (page) => {
      if (page >= 1 && page <= totalPages.value) {
        currentPage.value = page
        // Scroll to top of product grid
        document.querySelector('.product-grid')?.scrollIntoView({ 
          behavior: 'smooth', 
          block: 'start' 
        })
      }
    }
    
    const fullImageUrl = (path) => {
      if (!path) return ''
      return path.startsWith('http') ? path : `http://127.0.0.1:8000${path.startsWith('/') ? path : '/media/' + path}`
    }
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(2)
    }
    
    const truncate = (text, length) => {
      if (!text) return ''
      return text.length > length ? text.substring(0, length) + '...' : text
    }
    
    const resetSearch = () => {
      searchQuery.value = ''
    }
    
    // Generate a consistent rating for each product based on its ID
    const getProductRating = (product) => {
      return ((product.id * 7) % 3) + 3; // 3, 4, or 5
    }
    
    // Generate a random but consistent number of reviews
    const getReviewCount = (product) => {
      return ((product.id * 7) % 115) + 5;
    }
    
    // Check if product is "new"
    const isNewProduct = (product) => {
      return product.id % 4 === 0;
    }
    
    // Enhanced stock count badge functions
    const getStockCountBadgeClass = (product) => {
      if (product.stock === 0) return 'stock-count-out'
      if (product.stock < 5) return 'stock-count-low'
      if (product.stock < 20) return 'stock-count-medium'
      return 'stock-count-high'
    }
    
    
    const incrementQuantity = (product) => {
      if (!quantities[product.id]) {
        quantities[product.id] = 1
      }
      
      if (quantities[product.id] < product.stock) {
        quantities[product.id]++
      }
    }
    
    const decrementQuantity = (product) => {
      if (!quantities[product.id]) {
        quantities[product.id] = 1
      }
      
      if (quantities[product.id] > 1) {
        quantities[product.id]--
      }
    }
    
    const fetchProducts = async () => {
      console.log("Fetching products...")
      isLoading.value = true
      try {
        // Wait for auth to be initialized
        await auth.waitForInitialization()
        
        // Add a small delay to ensure API service is properly initialized
        await new Promise(resolve => setTimeout(resolve, 300))
        
        console.log("Auth initialized, fetching products...")
        const response = await axios.get('products/')
        console.log("Products fetched:", response.data.length)
        products.value = response.data
        
        // Initialize quantities
        products.value.forEach(product => {
          quantities[product.id] = 1
        })
      } catch (error) {
        console.error('Error fetching products:', error)
        toast.error('Failed to load products. Please try again.')
      } finally {
        isLoading.value = false
      }
    }
    
    const addToCart = (product) => {
      if (product.stock === 0) {
        toast.error('Sorry, this product is out of stock.')
        return
      }
      
      try {
        // Get existing cart from localStorage
        let cart = []
        const savedCart = localStorage.getItem('cart')
        
        if (savedCart) {
          cart = JSON.parse(savedCart)
        }
        
        // Check if product already in cart
        const existingItem = cart.find(item => item.product && item.product.id === product.id)
        const quantityToAdd = quantities[product.id] || 1
        
        if (existingItem) {
          // Check if adding the quantity would exceed stock
          if (existingItem.quantity + quantityToAdd <= product.stock) {
            existingItem.quantity += quantityToAdd
            toast.success(`Added ${quantityToAdd} ${product.name} to your cart.`)
          } else {
            toast.warning(`Cannot add ${quantityToAdd} more ${product.name}. Maximum stock reached.`)
            return
          }
        } else {
          // Add new item to cart
          cart.push({
            product: {
              id: product.id,
              name: product.name,
              price: product.price,
              description: product.description,
              image: product.image,
              stock: product.stock
            },
            quantity: quantityToAdd
          })
          toast.success(`Added ${quantityToAdd} ${product.name} to your cart!`)
        }
        
        // Save updated cart
        localStorage.setItem('cart', JSON.stringify(cart))
        
        // Reset quantity after adding to cart
        quantities[product.id] = 1
        
        // Trigger storage event for other components to detect
        window.dispatchEvent(new Event('storage'))
        
        // Notify about cart update
        notifyCartUpdate()
      } catch (err) {
        console.error('Error adding to cart:', err)
        toast.error('Failed to add product to cart.')
      }
    }
    
    onMounted(async () => {
      console.log("ProductList mounted")
      // Wait for auth initialization before fetching products
      await auth.waitForInitialization()
      await fetchProducts()
    })

    // Add a watcher to refetch products when auth state changes
    watch(() => auth.isLoggedIn, async (newValue, oldValue) => {
      console.log(`Auth state changed: ${oldValue} -> ${newValue}`)
      if (newValue && auth.isInitialized) {
        console.log("Auth is logged in and initialized, fetching products...")
        await fetchProducts()
      }
    })
    
    return {
      products,
      filteredProducts,
      paginatedProducts,
      isLoading,
      searchQuery,
      quantities,
      currentPage,
      productsPerPage,
      totalPages,
      startIndex,
      visiblePages,
      fullImageUrl,
      formatPrice,
      truncate,
      resetSearch,
      addToCart,
      incrementQuantity,
      decrementQuantity,
      getProductRating,
      getReviewCount,
      isNewProduct,
      getStockCountBadgeClass,
      goToPage
    }
  }
}
</script>


