import { reactive } from "vue"
import axios from "./axios"

// Create a global flag to track initialization status
let globalInitialized = false

const auth = reactive({
  isLoggedIn: false,
  isStaff: false,
  isInitialized: false,

  init() {
    console.log("Auth init called")
    const isDemoMode = window.location.hostname.includes("github.io") || process.env.VUE_APP_DEMO_MODE === "true"

    // Get auth state from localStorage
    this.isLoggedIn = !!localStorage.getItem("access")
    this.isStaff = localStorage.getItem("is_staff") === "true"

    console.log(`Auth initialized: loggedIn=${this.isLoggedIn}, isStaff=${this.isStaff}, isDemoMode=${isDemoMode}`)

    this.isInitialized = true
    globalInitialized = true
  },

  async checkTokenValidity() {
    // Skip check in demo mode
    if (process.env.VUE_APP_DEMO_MODE === "true" || window.location.hostname.includes("github.io")) {
      this.isInitialized = true
      globalInitialized = true
      return true
    }

    const token = localStorage.getItem("access")
    if (!token) {
      this.isInitialized = true
      globalInitialized = true
      return false
    }

    try {
      await axios.get("user/")
      this.isInitialized = true
      globalInitialized = true
      return true
    } catch (error) {
      console.log("Token invalid or expired, logging out")
      this.logout()
      return false
    }
  },

  logout(redirectToGithubPages = false) {
    console.log("Logout called")
    const cart = localStorage.getItem("cart")

    // Clear localStorage except cart
    localStorage.removeItem("access")
    localStorage.removeItem("refresh")
    localStorage.removeItem("is_staff")
    localStorage.removeItem("user_id")
    localStorage.removeItem("username")

    if (cart) localStorage.setItem("cart", cart)

    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = true // Keep as initialized but logged out
    globalInitialized = true

    if (redirectToGithubPages) {
      // This will be handled by the component using router
    }
  },

  // Add method to wait for initialization
  async waitForInitialization() {
    console.log("Wait for initialization called, current status:", this.isInitialized)

    if (this.isInitialized) {
      return Promise.resolve()
    }

    return new Promise((resolve) => {
      if (this.isInitialized) {
        resolve()
        return
      }

      const checkInterval = setInterval(() => {
        if (this.isInitialized || globalInitialized) {
          clearInterval(checkInterval)
          resolve()
        }
      }, 50)
    })
  },

  reinitialize() {
    console.log("Auth reinitialize called")
    this.isInitialized = false
    globalInitialized = false
    this.init()
  },
})

// Initialize immediately
auth.init()

export { auth }
