import axios from "axios"
import mockApiService from "./services/mockApiService"
import adminMockApiService from "./services/adminMockApiService"

// Check if we're in demo mode (GitHub Pages or local demo)
const isDemoMode = () => window.location.hostname.includes("github.io") || process.env.VUE_APP_DEMO_MODE === "true"

// Function to determine which API service to use
const getApiService = () => {
  if (!isDemoMode()) {
    // Use real axios in development
    return axios.create({
      baseURL: "http://127.0.0.1:8000/api/",
    })
  }

  // In demo mode, check if user is staff
  const isStaff = localStorage.getItem("is_staff") === "true"
  return isStaff ? adminMockApiService : mockApiService
}

// Create the instance
const instance = axios.create({
  baseURL: "http://127.0.0.1:8000/api/",
  timeout: 10000,
  headers: {
    "Content-Type": "application/json",
    Accept: "application/json",
  },
})

// Only add interceptors if using real axios
if (!isDemoMode() && instance.interceptors) {
  instance.interceptors.request.use(
    (config) => {
      const token = localStorage.getItem("access")
      if (token) {
        config.headers.Authorization = `Bearer ${token}`
      }
      return config
    },
    (error) => {
      return Promise.reject(error)
    },
  )

  instance.interceptors.response.use(
    (response) => {
      return response
    },
    (error) => {
      if (error.response && error.response.status === 401) {
        // Handle unauthorized errors (e.g., redirect to login)
        console.error("Unauthorized access:", error)
        localStorage.clear()
        window.location.href = "/login"
      }
      return Promise.reject(error)
    },
  )
}

// For demo mode, we need to handle dynamic API service switching
const dynamicApiService = {
  get: (url, config) => {
    const service = getApiService()
    return service.get(url, config)
  },
  post: (url, data, config) => {
    const service = getApiService()
    return service.post(url, data, config)
  },
  put: (url, data, config) => {
    const service = getApiService()
    return service.put(url, data, config)
  },
  patch: (url, data, config) => {
    const service = getApiService()
    return service.patch(url, data, config)
  },
  delete: (url, config) => {
    const service = getApiService()
    return service.delete(url, config)
  },
  request: (config) => {
    const service = getApiService()
    return service.request(config)
  },
}

// Export the appropriate service based on the mode
export default isDemoMode() ? dynamicApiService : instance
